import React from 'react';

class Register extends React.Component {

	constructor(){
		super()
		this.state={
			name:"",
			email:"",
			password:"",
			showErrorMessage:false
		}
	}

	onNameChange = (event)=>{
		//console.log(event.target.value);
		this.setState({name:event.target.value});
	}

	onEmailChange = (event)=>{
		//console.log(event.target.value);
		this.setState({email:event.target.value});
	}

	onPasswordChange = (event)=>{
		//console.log(event.target.value);
		this.setState({password:event.target.value});
	}

	onSubmissionClick =()=>{

		const updateStateUser = this.props.updateStateUser;
		fetch('https://rocky-bastion-39416.herokuapp.com/register' ,{
			method:'post',
			headers:{'Content-Type':'application/json'},
			body: JSON.stringify({name:this.state.name,
				 				  email:this.state.email,
								  password:this.state.password})
		})
		.then(res=>res.json())
		.then(data=>{
			// console.log(data);
			if(data.id){ updateStateUser(data);
						 this.props.onRouteChange('Home');}
			else{
				this.setState({showErrorMessage:true});
			}
		}) ;
	}


	render(){

		return (

			<main className="pa1 black-80">
				  <div className="measure center w-70 shadow-3 pa4">
				    <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
				      <legend className="f4 fw6 ph0 mh0">Register</legend>

				       <div className="mt3">
				        <label className="db fw6 lh-copy f6" htmlFor="Name">Name</label>
				        <input
				         className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
				         type="text" 
				         name="name"  
				         id="name"
				         onChange={this.onNameChange}
				          />
				      </div>

				      <div className="mv3">
				        <label className="db fw6 lh-copy f6" htmlFor="email-address">Email</label>
				        <input 
				        className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
				        type="email" 
				        name="email-address"  
				        id="email-address"
				        onChange={this.onEmailChange} 
				        />
				      </div>
				      <div className="mv3">
				        <label className="db fw6 lh-copy f6" htmlFor="password">Password</label>
				        <input 
				        className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
				        type="password" 
				        name="password"  
				        id="password"
				        onChange={this.onPasswordChange} 
				        />
				      </div>				    				      
				    </fieldset>

				    <div className="">
				      <input
				       onClick={this.onSubmissionClick }
				       className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib"
				       type="submit"
				       value="Sign up"
				        />
				    </div>	
				    {this.state.showErrorMessage ? <label className="pa0 ma0  f6 orange "> <p  > Something Wrong !!  </p></label> : " " }			    
				  </div>
			</main>

		
          )

	}
	

}

export default Register;